@charset "UTF-8";
:root {
  --font-family-base: "YuGothic", "游ゴシック", "Zen Kaku Gothic New", "yu-gothic-pr6n", sans-serif;
  --font-family-second: "Jockey One", sans-serif;
  --font-family-third: "Inter", sans-serif;
}

:root {
  --font-weight-light: 300;
  --font-weight-regular: 400;
  --font-weight-normal: 500;
  --font-weight-bold: 700;
}

:root {
  --black: #000;
  --text-black: #1E1E1E;
  --main-color: #9E895B;
  --main-black: #585757;
  --red: #FE3101;
  --white: #fff;
  --line-color: #ECECEC;
  --gradient-color: linear-gradient(90deg, #2C65FF 0%, #79F890 50%, #FF9 100%);
  --bg-color: #F7F9FB;
}

:root {
  --z-index-to-top: 200;
  --z-index-drawer: 100;
  --z-index-header: 1000;
  --z-index-modal: 10000;
}

:root {
  --section-padding-block: calc(50 / 16 * 1rem);
}

@media (min-width: 767px) {
  :root {
    --section-padding-block: calc(100 / 16 * 1rem);
  }
}
:root {
  --duration-short: 0.2s;
  --duration-base: 0.3s;
  --duration-long: 0.6s;
  --duration-fv: 0.7s;
}

body {
  color: var(--text-black);
  font-family: var(--font-family-base);
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.2903225806vw;
  }
}
@media (min-width: 1240px) {
  html {
    font-size: 16px;
  }
}

html {
  scroll-padding-top: 4.125rem;
}
@media screen and (min-width: 768px) {
  html {
    scroll-padding-top: 8.375rem;
  }
}

/* グローバルリセット */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* テキストサイズの調整をリセット */
html {
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
  scroll-behavior: smooth;
}

/* ボディの基本設定 */
body {
  margin: 0;
  overflow-wrap: anywhere;
  word-break: normal;
  line-break: strict;
  min-height: 100vh;
  text-rendering: optimizeLegibility;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* デフォルトの余白とパディングを削除 */
ul,
ol,
button,
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd,
time,
small {
  margin: 0;
  padding: 0;
}

/* リストスタイルのリセット */
ul,
ol {
  list-style: none;
}

/* クラスなしのaタグにデフォルトのスタイルを適用 */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* インラインブロック要素 */
a,
span,
button {
  display: inline-block;
}

/* フォントとマージンのリセット */
small,
time,
figure {
  border: 0;
  font: inherit;
  font-size: 100%;
  vertical-align: baseline;
}

address {
  font-style: normal;
}

/* 画像の調整 */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* 記事内の自然な余白を設定 */
article > * + * {
  margin-top: 1em;
}

/* フォーム要素のフォント継承 */
input,
button,
textarea,
select {
  font: inherit;
}

/* フォームのリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  outline: none;
}

textarea {
  resize: vertical;
}

/* チェックボックスとラジオボタンを非表示 */
input[type=checkbox],
input[type=radio] {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  clip-path: polygon(0 0, 0 0, 0 0, 0 0);
  border: 0;
}

/* ボタンやラベルにカーソルを設定 */
input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

/* IE用の選択ボックスアイコンを非表示 */
select::-ms-expand {
  display: none;
}

/* aタグのスタイルとトランジション */
a {
  text-decoration: none;
  color: inherit;
  transition: 0.3s;
}

button {
  color: inherit;
}

/* ホバー時のカーソル設定 */
@media (any-hover: hover) {
  a:hover {
    cursor: pointer;
  }
}
/* PCでtelリンクを無効化 */
@media (min-width: 768px) {
  a[href*="tel:"] {
    pointer-events: none;
    cursor: default;
    text-decoration: none;
  }
}
/* タップ時のハイライトを透明に設定 */
a {
  -webkit-tap-highlight-color: transparent;
  /* リンクのハイライトを透明に */
}

button {
  -webkit-tap-highlight-color: transparent;
  /* ボタンのハイライトも透明に */
}

