/* ===============================================
# フェードアニメーション：IntersectionObserver＆GSAP用
=============================================== */
/* フェード */
.js-fade-in {
  opacity: 0;
  transition: opacity 2s ease-out;
}
.js-fade-in.is-active {
  opacity: 1;
}

/* 上方向にスライド */
.js-fade-up {
  opacity: 0;
  translate: 0 20px;
  transition: opacity var(--duration-long) ease, translate var(--duration-long) ease;
}
.js-fade-up.is-active {
  opacity: 1;
  translate: 0 0;
}

/* 左方向にスライド */
.js-slide-left {
  opacity: 0;
  translate: -50px 0;
  transition: opacity 1.2s ease-out, translate 1.2s ease-out;
}
.js-slide-left.is-active {
  opacity: 1;
  translate: 0 0;
}

/* 右方向にスライド */
.js-slide-right {
  opacity: 0;
  translate: 10px 0;
  transition: opacity var(--duration-long) ease, translate var(--duration-long) ease;
}
@media screen and (min-width: 768px) {
  .js-slide-right {
    translate: 20px 0;
  }
}
.js-slide-right.is-active {
  opacity: 1;
  translate: 0 0;
}

.js-scaleImg {
  overflow: hidden;
}
.js-scaleImg img {
  scale: 1.2;
  transition: scale 0.8s ease, opacity 0.8s cubic-bezier(0.48, 0.07, 0.33, 0.79);
  opacity: 0;
}
.js-scaleImg.is-active img {
  scale: 1;
  opacity: 1;
}

/* クリップパス */
.js-clip-img {
  overflow: hidden;
}
.js-clip-img img {
  scale: 1.3;
  transition: scale 1s cubic-bezier(0.48, 0.07, 0.33, 0.79), clip-path 1s cubic-bezier(0.48, 0.07, 0.33, 0.79);
  clip-path: inset(0 100% 0 0);
}
.js-clip-img.is-active img {
  clip-path: inset(0 0 0 0);
  scale: 1;
}
@media screen and (min-width: 768px) {
  .js-clip-img img {
    transition: scale 1s cubic-bezier(0.75, 0.45, 0.53, 0.76), clip-path 1s cubic-bezier(0.75, 0.45, 0.53, 0.76);
  }
}

