.section-title.is-active::before {
  opacity: 1;
  left: calc(50% - 10px);
}
@media screen and (min-width: 768px) {
  .section-title.is-active::before {
    left: 0;
  }
}
.section-title::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 2.0625rem;
  height: 0.25rem;
  background: var(--gradient-color);
  transition: left var(--duration-long) ease, opacity var(--duration-long) ease;
  transition-delay: 0.3s;
  opacity: 0;
}
@media (max-width: 767px) {
  .section-title::before {
    translate: -50% 0;
  }
}
@media screen and (min-width: 768px) {
  .section-title::before {
    left: 10px;
  }
}
.section-title span {
  opacity: 0;
  transform: translateX(10px);
  transition: opacity var(--duration-long) ease, transform var(--duration-long) ease;
  transition-delay: calc(0.02s * var(--index));
  width: fit-content;
  letter-spacing: 0;
  display: inline-block;
}
.section-title.is-active span {
  opacity: 1;
  transform: translateX(0);
}

.section-title.section-title--center {
  width: fit-content;
  margin-inline: auto;
}
.section-title.section-title--center::before {
  left: 50%;
  transform: translateX(-50%);
}
.section-title.section-title--center.is-active::before {
  left: calc(50% - 10px);
}